<template>
  <div class="image-list">
    <div style="padding-bottom:20px">
      <el-radio-group
        size="small"
        v-model="collect"
        @change="loadImages(1)"
      >
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <el-button
        v-if="isShowAdd"
        style="float: right"
        size="small"
        type="success"
        @click=" dialogUploadVisible = true"
      ></el-button>
    </div>
    <!--  -->
    <el-row :gutter="0">
      <el-col
        class="img-item"
        :lg="4"
        :xs="12"
        :sm="6"
        :md="6"
        v-for="(img, index) in images"
        :key="index"
        @click.native="selected = index"
      >
        <el-image
          style="height: 100%;width:100%"
          :src="img.url"
          fit="cover"
        ></el-image>
        <div
          class="selected"
          v-if="isShowSelected && selected === index"
        ></div>
        <div
          class="image-action"
          v-if="isShowAction"
        >
          <el-button
            class="image-btn"
            type="warning"
            size="small"
            :icon="img.is_collected ? 'el-icon-star-on': 'el-icon-star-off'"
            :loading="img.loading"
            circle
            @click="onCollect(img)"
          ></el-button>
          <el-button
            class="image-btn"
            size="small"
            type="danger"
            icon="el-icon-delete"
            :loading="img.loading"
            circle
            @click="onDelete(img)"
          ></el-button>
          <!-- <i
              :class="{
              'el-icon-star-on':,
              'el-icon-star-off':!img.is_collected
            }"
          ></i>-->
          <!-- <i class="el-icon-delete"></i> -->
        </div>
      </el-col>
    </el-row>
    <!-- / -->
    <!--  -->
    <!--   -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="pageSize"
      :current-page.sync="page"
      @current-change="onPageChange"
    ></el-pagination>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogUploadVisible"
      :append-to-body="true"
    >
      <!--
      upload 
      post
      action 
      headers
      -->
      <el-upload
        :show-file-list="false"
        class="upload-demo"
        drag
        action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
        :headers="uploadHeaders"
        name="image"
        multiple
        :on-success="onUploadSuccess"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          
          <em></em>
        </div>
        <div
          class="el-upload__tip"
          slot="tip"
        >jpg/png500kb</div>
      </el-upload>
    </el-dialog>
  </div>
</template>
<script>
import { getImages, collectImage, deleteImage } from '@/api/image.js'
export default {
  name: 'ImageList',
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, // 
      images: [],
      dialogUploadVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      totalCount: 0, // 
      pageSize: 24, // 
      page: 1, // 
      selected: null // 
    }
  },
  components: {},
  props: {
    isShowAdd: {
      type: Boolean,
      default: true
    },
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  computed: {},
  watch: {},
  created () {
    // 
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    loadImages (page = 1) {
      // 
      this.page = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        const results = res.data.data.results
        results.forEach(img => {
          //  loading loading
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },
    onUploadSuccess () {
      // 
      this.dialogUploadVisible = false
      // 
      this.loadImages(this.page) // 
      this.$message({
        type: 'success',
        message: ''
      })
    },
    onPageChange (page) {
      this.loadImages(page)
    },
    onCollect (img) {
      img.loading = true
      //  
      collectImage(img.id, !img.is_collected).then(res => {
        img.is_collected = !img.is_collected
        this.loadImages(this.page) // 
        img.loading = false
      })
      // 
    },
    onDelete (img) {
      img.loading = true
      deleteImage(img.id).then(res => {
        this.loadImages(this.page) // 
        img.loading = false
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .upload-demo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .selected {
    position: absolute;
    background: url(./selected.png) no-repeat;
    background-size: contain;
    width: 80px;
    height: 80px;
  }
  .img-item {
    height: 120px;
    width: 150px;
    position: relative;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    .image-action {
      height: 35px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      background-color: rgba(108, 108, 108, 0.7);
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      font-size: 18px;
      color: whitesmoke;
      .image-btn {
        background: transparent;
        border: 0;
        font-size: 20px;
      }
      /deep/.el-icon-star-on {
        font-size: 24px !important;
        color: orange;
      }
    }
  }
</style>
