<template>
  <div class="article-container">
    <el-card class="filter-card">
      <div
        slot="header"
        class="clearfix"
      >
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!--  -->
      <el-form
        ref="form"
        size="mini"
        label-width="40px"
      >
        <el-form-item label="">
          <el-radio-group
            v-model="status"
            @change="loadArticle(1)"
          >
            <!-- el-radiolabelvalue
            -->
            <el-radio :label="null"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="channelId"
            placeholder=""
            @change="loadArticle(1)"
          >
            <el-option
              label=""
              :value="null"
            ></el-option>
            <el-option
              v-for="(channel, index) in channels"
              :key="index"
              :label="channel.name"
              :value="channel.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker
              v-model="rangeDate"
              type="datetimerange"
              start-placeholder=""
              end-placeholder=""
              :default-time="['12:00:00']"
              format="yyyy-MM-DD"
              value-format="yyyy-MM-DD"
              @change="loadArticle(1)"
            />
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <!-- buttonclick
          "isTrusted":true
          -->
          <el-col
            :span="4"
            class="input-wrap"
          >
            <el-input
              class="search-body"
              v-model="searchKey"
              clearable
              placeholder=""
              @clear="loadArticle(1)"
            ></el-input>
            <el-button
              :disabled="loading"
              @click="onSearchArticle(1)"
            ></el-button>
          </el-col>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card class="box-card">
      <div
        slot="header"
        class="clearfix"
      >{{totalCount}}</div>
      <!-- 
      table 
      1.data
      v-for 
      2.  el-table-column width
      label prop 
      3. 
      
      -->
      <el-table
        class="list-table"
        :data="articles"
        v-loading="loading"
        style="width: 100%"
        size="small"
        stripe
      >
        <el-table-column label="">
          <template slot-scope="scope">
            <!-- <img
              class="article-cover"
              :src="scope.row.cover.images[0] || './picerror.jpg'"
              alt
            />-->
            <!--  <img
              v-if="scope.row.cover.images[0]"
              class="article-cover"
              :src="scope.row.cover.images[0]"
              alt
            />
            <img
              v-else
              class="article-cover"
              src="./picLoading.gif"
              alt
            />-->
            <el-image
              class="article-cover"
              style="width: 100px; height: 100px"
              :src="scope.row.cover.images[0]"
              fit="scale-down"
              lazy
            >
              <div slot="placeholder">
                <i class="el-icon-loading isLoading"></i>
              </div>

              <div slot="error">
                <i
                  class="el-icon-picture-outline pic-error"
                  title
                ></i>
                <p></p>
              </div>
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
        ></el-table-column>
        <el-table-column label="">
          <!-- 
        template  slot-scope="scope"
          -->
          <template slot-scope="scope">
            <el-tag
              :type="articleStatus[scope.row.status].type"
            >{{articleStatus[scope.row.status].text}}</el-tag>
            <!-- <el-tag
              v-if="scope.row.status === 1"
              type="warning"
            ></el-tag>
            <el-tag
              v-if="scope.row.status === 2"
              type="success"
            ></el-tag>
            <el-tag
              v-if="scope.row.status === 3"
              type="danger"
            ></el-tag>
            <el-tag
              v-if="scope.row.status === 4"
              type="info"
            ></el-tag>-->
          </template>
        </el-table-column>
        <el-table-column
          prop="pubdate"
          label=""
        ></el-table-column>
        <el-table-column
          prop="address"
          label=""
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              circle
              icon="el-icon-edit"
              @click="$router.push('/editarticle?id=' + scope.row.id.toString())"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              circle
              icon="el-icon-delete"
              @click="onDeleteArticle(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 
      total 10
      pagesize:10
      -->
      <el-pagination
        layout="prev, pager, next"
        background
        :total="totalCount"
        :page-size="pageSize"
        :disabled="loading"
        :current-page.sync="page"
        @current-change="onCurrentChange"
      />
    </el-card>
  </div>
</template>
<script>
import { getArticle, getChannels, deleteArticle, searchArticle } from '@/api/article.js'
export default {
  name: 'articleIndex',
  data () {
    return {
      articles: [], // 
      articleStatus: [
        {
          status: 0,
          text: '',
          type: 'info'
        },
        {
          status: 1,
          text: '',
          type: ''
        },
        {
          status: 2,
          text: '',
          type: 'success'
        },
        {
          status: 3,
          text: '',
          type: 'warning'
        },
        {
          status: 4,
          text: '',
          type: 'danger'
        }
      ],
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, // 
      channels: [], // 
      channelId: null, // ID
      rangeDate: null, // 
      loading: true, // loading
      page: 1,
      searchKey: ''
    }
  },
  components: {},
  props: {},
  computed: {},
  watch: {},
  created () {
    this.loadArticle()
    this.loadChannels()
  },
  mounted () {},
  methods: {
    loadArticle (page = 1) {
      this.loading = true
      getArticle({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then(res => {
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount

        // loading
        this.loading = false
      })
    },
    onCurrentChange (page) {
      this.loadArticle(page)
    },
    loadChannels () {
      getChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onDeleteArticle (articleId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          deleteArticle(articleId.toString()).then(res => {
            this.$message({
              showClose: true,
              type: 'success',
              message: '!'
            })
            // 
            this.loadArticle(this.page)
          })
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
    },
    onSearchArticle (page = 1) {
      searchArticle({
        q: this.searchKey,
        page: page,
        per_page: this.pageSize
      }).then(res => {
        console.log(res)
        const { results } = res.data.data
        this.articles = results
        this.page = 1
        this.status = null
        this.channelId = null
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .filter-card {
    margin-bottom: 30px;
  }
  .list-table {
    margin-bottom: 20px;
  }
  .article-cover {
    width: 100px;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #ccc;
  }
  /deep/.pic-error,
  .isLoading {
    font-size: 30px !important;
  }
  .input-wrap {
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    .search-body {
      margin-right: 15px;
    }
  }
</style>
