/*  */
import request from '@/utils/request.js'
// 
export const getArticle = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/articles',
    params
    // bodydata
    // queryparams
    // headersheaders
  })
}

// 
export const getChannels = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/channels',
    params
  })
}

// 
export const deleteArticle = articleId => {
  return request({
    method: 'DELETE',
    url: `/mp/v1_0/articles/${articleId}`
    // url
    // :xxx 
  })
}

// 
export const publishArticle = (data, draft = false) => {
  return request({
    method: 'POST',
    url: '/mp/v1_0/articles',
    params: { draft }, // true  false true 
    data
  })
}

// 
export const updateArticle = (articleId, data, draft = false) => {
  return request({
    method: 'PUT',
    url: `/mp/v1_0/articles/${articleId}`,
    params: { draft }, // true  false true 
    data
  })
}

// 
export const getEditArticle = articleId => {
  return request({
    method: 'GET',
    url: `/mp/v1_0/articles/${articleId}`
  })
}

// 
export const updataCommentStatus = (articleId, allowComment) => {
  return request({
    method: 'PUT',
    url: '/mp/v1_0/comments/status',
    params: {
      article_id: articleId
    },
    data: {
      allow_comment: allowComment
    }
  })
}

// 
export const searchArticle = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/search',
    params
  })
}

// 
export const getTotalCount = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/statistics/articles',
    params
  })
}
