<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div
        slot="header"
        class="clearfix"
      >
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item>{{$route.query.id? '':''}}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form
        ref="publish-form"
        :model="article"
        label-width="60px"
        :rules="formRules"
      >
        <el-form-item
          label=""
          prop="title"
        >
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="content"
        >
          <!-- <el-input
            type="textarea"
            v-model="article.content"
          ></el-input>-->
          <el-tiptap
            lang="zh"
            height="350"
            placeholder=""
            v-model="article.content"
            :extensions="extensions"
          ></el-tiptap>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!-- <template v-if="article.cover.type > 0">
            <UploadCover
              v-for="(cover,index) in article.cover.type"
              :key="cover"
              :cover-image="article.cover.images[index]"
              @update-cover="onUpdateCover(index,url)"
            />
          </template>-->
          <template v-if="article.cover.type > 0">
            <div class="upload-cover-wrap">
              <UploadCover
                v-for="(cover,index) in article.cover.type"
                :key="cover"
                v-model="article.cover.images[index]"
              />
            </div>
          </template>
        </el-form-item>
        <el-form-item
          label=""
          prop="channel_id"
        >
          <el-select
            v-model="article.channel_id"
            placeholder=""
          >
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for="(channel, index) in channels"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            @click="onPublish(false)"
          >{{$route.query.id? '':''}}</el-button>
          <el-button
            v-if="$route.query.id"
            @click="onCancel"
          ></el-button>
          <el-button
            v-else
            @click="onPublish(true)"
          ></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import UploadCover from './components/upload-cover.vue'
import { getChannels, publishArticle, getEditArticle, updateArticle } from '@/api/article.js'
import { uploadImage } from '@/api/image.js'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  HorizontalRule,
  Fullscreen,
  Preview,
  Image,
  CodeBlock,
  Blockquote,
  TextColor,
  TextHighlight
} from 'element-tiptap'
export default {
  name: 'PublishIndex',
  data () {
    return {
      channels: [], // 
      article: {
        title: '', // 
        content: '', // 
        cover: {
          type: 1, // 
          images: [] // 
        },
        channel_id: null
      },
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Image({
          // base64
          uploadRequest (file) {
            // Content-Typemultipart/form-data
            // FormData()
            const fd = new FormData()
            fd.append('image', file)
            // returnpromise
            return uploadImage(fd).then(res => {
              console.log(res)
              // return
              return res.data.data.url
            })
          }
        }),
        new Underline({ bubble: true, menubar: false }), // 
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new HorizontalRule(),
        new TodoItem(),
        new TodoList(),
        new Fullscreen(),
        new Preview(),
        new CodeBlock(),
        new Blockquote(),
        new TextColor(),
        new TextHighlight()
      ],
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          {
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [{ required: true, message: '' }]
      }
    }
  },
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  props: {},
  watch: {},
  computed: {},
  created () {
    this.loadChannels()

    //  
    // id
    if (this.$route.query.id) {
      this.loadArticle()
    } else if (this.$route.query.id) {
      this.article = {
        title: '', // 
        content: '', // 
        cover: {
          type: 1, // 
          images: [] // 
        }
      }
    }
  },
  mounted () {},
  methods: {
    onCancel () {
      this.$route.query.id = ''
      this.$router.push('/article')
    },
    onPublish (draft = false) {
      this.$refs['publish-form'].validate(valid => {
        if (!valid) {
          return false
        }
        // 
        const articleId = this.$route.query.id
        if (articleId) {
          // 
          updateArticle(articleId, this.article, draft).then(res => {
            this.$message({
              message: '',
              type: 'success'
            })
            // 
            this.$router.push('/article')
          })
        } else {
          publishArticle(this.article, draft).then(res => {
            this.$message({
              message: draft === false ? '' : '',
              type: 'success'
            })
            // 
            this.$router.push('/article')
          })
        }
      })
    },
    loadChannels () {
      getChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    loadArticle () {
      // ID
      getEditArticle(this.$route.query.id).then(res => {
        // 
        this.article = res.data.data
      })
    },
    onUpdateCover (index, url) {
      this.article.cover.images[index] = url
    }
  }
}
</script>
<style lang="less" scoped>
  .upload-cover-wrap {
    display: flex;
    justify-content: start;
    // flex-direction: column;
    align-items: center;
  }
</style>
