<template>
  <div class="login-container">
    <div class="login-form-wrap">
      <div class="login-head">
        <div class="logo"></div>
      </div>
      <!-- 
      1.el-formmodel
      2.prop
      3.rules

      
      1.ref
      2. refvalidate
      -->
      <el-form
        class="login-form"
        ref="login-form"
        :model="user"
        :rules="formRules"
      >
        <el-form-item prop="mobile">
          <el-input
            v-model="user.mobile"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input
            v-model="user.code"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="agree">
          <el-checkbox v-model="user.agree"></el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button
            class="login-btn"
            type="primary"
            :loading="loginLoading"
            @click="onLogin"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { userLogin } from '@/api/user.js'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810', // 
        agree: false // 
      },
      loginLoading: false, // loading
      formRules: {
        // 
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[3|5|7|8|9]\d{9}$/, message: '', trigger: 'change' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{6}$/, message: '', trigger: 'change' }
        ],
        agree: [
          // callback
          // callback(new error(''))
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  components: {},
  props: {},
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onLogin () {
      // 
      // const user = this.user
      // 
      this.$refs['login-form'].validate(valid => {
        // 
        if (!valid) {
          return false
        }
        this.login()
      })
    },
    login () {
      // loading 
      this.loginLoading = true
      // 
      userLogin(this.user)
        .then(result => {
          console.log(result)
          this.$message({
            message: '',
            type: 'success'
          })
          // loading
          this.loginLoading = false
          // 
          window.localStorage.setItem('user', JSON.stringify(result.data.data))
          this.$router.push({
            name: 'home'
          })
        })
        .catch(err => {
          console.log(err)
          this.$message.error('')
          // loading
          this.loginLoading = false
        })
      // 
    }
  }
}
</script>

<style lang="less" scoped>
  .login-container {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    //
    background: url('./bgimage.jpg') no-repeat;
    background-size: cover;
    .login-form-wrap {
      min-width: 300px;
      padding: 20px 30px 0px;
      background-color: #fff;
      .login-head {
        display: flex;
        justify-content: center;
        .logo {
          width: 200px;
          height: 57px;
          background: url('./logo_index.png') no-repeat;
          background-size: contain;
        }
      }
      .login-form {
        background-color: #fff;
        padding: 20px 20px 10px 20px;
        min-width: 300px;
        .login-btn {
          width: 100%;
        }
      }
    }
  }
</style>
