<template>
  <div class="comment-container">
    <el-card class="box-card">
      <div
        slot="header"
        class="clearfix"
      >
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!--  -->
      <div>
        <!-- data -->
        <!--  -->
        <!-- -->
        <el-table
          class="table-list"
          :data="articles"
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="title"
            label=""
          ></el-table-column>
          <el-table-column
            prop="total_comment_count"
            label=""
          ></el-table-column>
          <el-table-column
            prop="fans_comment_count"
            label=""
          ></el-table-column>
          <el-table-column
            label=""
          >
            <template slot-scope="scope">{{scope.row.comment_status ? '':''}}</template>
          </el-table-column>

          <el-table-column
            label=""
          >
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.comment_status"
                active-color="#13ce66"
                inactive-color="#ff4949"
                :disabled="scope.row.statusDisabled"
                @change="onStatusChange(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!--  -->
      <!--  -->
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size.sync="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount"
        background
      />
    </el-card>
  </div>
</template>
<script>
import { getArticle, updataCommentStatus } from '@/api/article.js'
export default {
  name: 'CommentIndex',
  data () {
    return {
      currentPage: 1, // 
      articles: [],
      totalCount: 0, // 
      pageSize: 10 // 
    }
  },
  components: {},
  props: {},
  computed: {},
  watch: {},
  created () {
    this.loadArticles()
  },
  mounted () {},
  methods: {
    handleSizeChange () {
      this.loadArticles(1)
    },
    handleCurrentChange (page) {
      // 
      this.loadArticles(page)
    },
    loadArticles (page = 1) {
      this.currentPage = page// 
      getArticle({
        response_type: 'comment',
        page,
        per_page: this.pageSize
      }).then(res => {
        const { results } = res.data.data
        results.forEach(article => {
          article.statusDisabled = false
        })
        this.articles = results
        this.totalCount = res.data.data.total_count
      })
    },
    onStatusChange (article) {
      article.statusDisabled = true
      updataCommentStatus(article.id.toString(), article.comment_status).then(res => {
        article.statusDisabled = false
        this.$message({
          type: 'success',
          message: article.comment_status ? '' : ''
        })
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .table-list {
    margin-bottom: 20px;
  }
</style>
