<template>
  <el-container class="layout-container">
    <el-aside
      class="aside"
      width="auto"
    >
      <AppAside
        class="aside-menu"
        :is-collapse="isCollapse"
      />
    </el-aside>
    <el-container>
      <el-header class="header">
        <div>
          <i
            class="fold-btn"
            :class="{
              'el-icon-s-fold':!isCollapse,
              'el-icon-s-unfold':isCollapse
            }"
            @click="isCollapse = !isCollapse"
          ></i>
          <span class="header-title"></span>
        </div>
        <el-dropdown>
          <div class="user-info">
            <img
              class="user-photo"
              :src="user.photo"
              alt
            />
            <span class="user-name">{{user.name}}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="$router.push('/settings')"></el-dropdown-item>
            <!--  -->
            <el-dropdown-item @click.native="onLogout"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-main class="main">
        <!--  -->
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import AppAside from './components/aside.vue'
import { getUserProfile } from '@/api/user.js'
import globalBus from '@/utils/global-bus.vue'

export default {
  name: 'LayoutIndex',
  data () {
    return {
      user: {}, // 
      isCollapse: false // 
    }
  },
  components: { AppAside },
  props: {},
  computed: {},
  watch: {},
  created () {
    this.loadUserProfile()
    //  
    globalBus.$on('update-user', data => {
      // this.user = data 
      this.user.name = data.name
      this.user.photo = data.photo
    })
  },
  mounted () {},
  methods: {
    // 
    loadUserProfile () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    onLogout () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            showClose: true,
            type: 'success',
            message: '!'
          })
          window.localStorage.removeItem('user')
          this.$router.push('/login')
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>
<style lang="less" scoped>
  .layout-container {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  .aside {
    background-color: #d3dce6;
    .aside-menu {
      height: 100%;
    }
  }
  .header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ccc;
    /deep/.fold-btn {
      font-size: 23px;
      margin-right: 10px;
      color: #1d7dfa;
    }
    .header-title {
      font-size: 20px;
      font-family: PingFang SC;
      color: #1d7dfa;
      font-weight: 500;
      vertical-align: text-bottom;
    }
    .user-info {
      display: flex;
      align-items: center;
      .user-photo {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
  }
  .main {
    background-color: #e9eef3;
  }
</style>
