<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div
        slot="header"
        class="clearfix"
      >
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row>
        <el-col :span="15">
          <el-form
            :rules="rules"
            ref="profile-form"
            :model="userProfile"
            label-width="80px"
            label-position="left"
          >
            <el-form-item label="">{{userProfile.id}}</el-form-item>
            <el-form-item label="">{{userProfile.mobile}}</el-form-item>
            <el-form-item
              label=""
              prop="name"
            >
              <el-input v-model="userProfile.name"></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="intro"
            >
              <el-input
                type="textarea"
                v-model="userProfile.intro"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="email"
            >
              <el-input v-model="userProfile.email"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button
                type="success"
                icon="el-icon-edit"
                :loading="loadingProfile"
                @click="onUpdateUserInfo"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col
          :offset="4"
          :span="4"
        >
          <label for="file">
            <el-avatar
              shape="square"
              :size="150"
              fit="cover"
              :src="userProfile.photo"
            ></el-avatar>

            <el-button
              style="width:150px"
              @click="$refs.file.click()"
              icon="el-icon-upload"
            ></el-button>
          </label>
          <input
            id="file"
            type="file"
            ref="file"
            hidden
            @change="onFileChange"
          />
        </el-col>
      </el-row>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      width="30%"
      @opened="onDialogOpened"
      @closed="onDialogClosed"
    >
      <div class="preview-image-wrap">
        <img
          class="preview-image"
          ref="previewImage"
          :src="previewImage"
        />
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          :loading="loadingAvatar"
          @click="onUpdatePhoto"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getUserProfile, updateUserPhoto, updateUserProfile } from '@/api/user.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus.vue'

export default {
  name: 'SettingIndex',
  data () {
    return {
      rules: {
        name: [
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'change' },
          { required: true, message: '', trigger: 'change' }
        ],
        email: [
          { required: true, message: '', trigger: 'change' },
          { type: 'email', message: '', trigger: ['blur', 'change'] }
        ],
        intro: [
          { required: true, message: '', trigger: 'change' },
          { min: 1, max: 100, message: ' 1  60', trigger: 'change' }
        ]
      },
      userProfile: {
        email: '',
        id: null,
        intro: '',
        mobile: '',
        name: '',
        photo: ''
      },
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: null, // 
      loadingAvatar: false, // loading
      loadingProfile: false // loading
    }
  },
  components: {},
  props: {},
  computed: {},
  watch: {},
  created () {
    this.loadUserProfile()
  },
  mounted () {},
  methods: {
    loadUserProfile () {
      getUserProfile().then(res => {
        this.userProfile = res.data.data
      })
    },
    onFileChange () {
      // 
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      this.dialogVisible = true
      /*
      1. 
      2. filesblob
      3. blobimgsrc
      */
      this.$refs.file.value = '' //   change
    },

    onDialogOpened () {
      // 
      // imgimg
      // 
      // 
      // 
      const image = this.$refs.previewImage
      // 
      // 
      // 
      // 1.replace 
      // 2.v-if 
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return false
      }
      this.cropper = new Cropper(image, {
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'none',
        cropBoxMovable: false,
        cropBoxResizable: false,
        background: false,
        movable: true
      })
    },
    onDialogClosed () {
      // 
      // this.cropper.destroy()
    },
    onUpdatePhoto () {
      // loading
      this.loadingAvatar = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        // 
        // fd
        updateUserPhoto(fd)
          .then(res => {
            // 
            this.dialogVisible = false
            // 
            // this.userProfile.photo = res.data.data.photo
            this.userProfile.photo = window.URL.createObjectURL(file)
            this.$message.success('')
            this.loadingAvatar = false // loading
            // 
            globalBus.$emit('update-user', this.userProfile)
          })
          .catch(() => {
            this.dialogVisible = false
            this.$message.error('~')
          })
      })
    },
    onUpdateUserInfo () {
      this.$refs['profile-form'].validate(valid => {
        // 
        if (!valid) {
          return false
        }
        updateUserProfile({ name, intro, email })
      })
      // loading
      this.loadingProfile = true
      const { name, intro, email } = this.userProfile
      updateUserProfile({ name, intro, email }).then(res => {
        this.$message.success('')
      })
      // loading
      this.loadingProfile = false
      // 
      globalBus.$emit('update-user', this.userProfile)
    }
  }
}
</script>
<style lang="less" scoped>
  .preview-image-wrap {
    .preview-image {
      display: block;
      max-width: 100%;
    }
  }
</style>
